import AbrEntityType from './AbrEntityType';

enum EntityType {
  None = 0,
  Individual = 1,
  Partnership = 3,
  Company = 5,
  Trust = 7,
}

export const EntityTypeLabel = new Map<EntityType, string>([
  [EntityType.None, ''],
  [EntityType.Individual, 'Individual'],
  [EntityType.Partnership, 'Partnership'],
  [EntityType.Company, 'Company'],
  [EntityType.Trust, 'Trust'],
]);

export const EntityTypeAbrEntityTypeMapping = new Map<AbrEntityType, EntityType>([
  [AbrEntityType.None, EntityType.None],
  [AbrEntityType.AustralianPrivateCompany, EntityType.Company],
  [AbrEntityType.AustralianPublicCompany, EntityType.Company],
  [AbrEntityType.CorporateUnitTrust, EntityType.Trust],
  [AbrEntityType.DiscretionaryInvestmentTrust, EntityType.Trust],
  [AbrEntityType.DiscretionaryTradingTrust, EntityType.Trust],
  [AbrEntityType.FamilyPartnership, EntityType.Partnership],
  [AbrEntityType.FixedTrust, EntityType.Trust],
  [AbrEntityType.FixedUnitTrust, EntityType.Trust],
  [AbrEntityType.HybridTrust, EntityType.Trust],
  [AbrEntityType.IndividualSoleTrader, EntityType.Individual],
  [AbrEntityType.OtherTrust, EntityType.Trust],
  [AbrEntityType.Partnership, EntityType.Partnership],
  [AbrEntityType.SelfManagedSuperannuationFund, EntityType.Trust],
]);

export default EntityType;
