import { Button, Grid } from "@material-ui/core";
import React, { useState } from "react";
import { CompanyTrustee, isCompanyTrustee, Trust, Trustee } from "types/Trust";
import { FormLabel } from '@material-ui/core';
import styled from "@emotion/styled";
import { css } from "emotion";
import Card from "components/Card";
import { MdAdd, MdPerson } from "react-icons/md";
import { BiBuilding } from "react-icons/bi";
import AutoCompleteField, { SelectOption } from "components/form/AutoCompleteField";
import TrustType, { TrustTypeLabels } from "types/TrustType";
import { useForm } from "react-hook-form";
import UploadField from "components/form/UploadField";
import TextField from "components/form/TextField";
import AddCompanyTrusteeDialog from "./AddCompanyTrusteeDialog";
import CompanyTrusteeDetails from "./CompanyTrusteeDetails";

const Trustees = styled.div`
`;

const Header = styled.div`
  display: flex;
`;

const LabelStyle = css`
  flex-grow: 1;
  font-size: 11px;
  color: #777;
  line-height: 2em;
  align-self: flex-end;
`;

const Actions = styled.div`
  .MuiButton-root {
    margin-left: 8px;
  }
`;

const EmptyState = styled.div`
  background-color: #eee;
  padding: 24px;
  text-align: center;
`;


const trustTypeOptions = [...TrustTypeLabels.entries()].map(([trustType, label]) => 
  ({ label: label, value: trustType })
);

export interface TrustDetailsProps {
  trust?: Trust;
  // onChange: (tradingEntity?: TradingEntity) => void;
}

interface TrustForm {
  Name?: string;
  TrustType?: SelectOption<TrustType>;
  TrustDeed?: (File | string)[];
}

const defaultValues: TrustForm = {
}

function TrustDetails({ trust }: TrustDetailsProps) {
  const [isCompanyModalOpen, setIsCompanyModalOpen] = useState(false);
  const [trustees, setTrustees] = useState<Trustee[]>([]);

  const methods = useForm<TrustForm>({
    defaultValues,
    mode: 'onChange',
  });

  const {
    // handleSubmit,
    control,
    // watch,
    // getValues,
  } = methods;

  function onCompanyModalClose() {
    setIsCompanyModalOpen(false);
  }

  function onCompanyModalSubmit(company: CompanyTrustee) {
    setIsCompanyModalOpen(false);
    addTrustee(company);
  }

  function onAddCompany() {
    setIsCompanyModalOpen(true);
  }

  function addTrustee(trustee: Trustee) {
    setTrustees([...trustees, trustee]);
  }

  function removeTrustee(index: number) {
    setTrustees(trustees.slice(0, index).concat(trustees.slice(index + 1)));
  }

  return (
    <Card title="Trust Details">
      <Grid container spacing={2}>
        <Grid item xs={12} sm={6}>
          <TextField
            name="Name"
            control={control}
            label="Name"
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <AutoCompleteField
            name="TrustType"
            control={control}
            label="Type"
            options={trustTypeOptions}
          />
        </Grid>
        <Grid item xs={12} sm={6}>
          <UploadField
            name="TrustDeed"
            label="Certified Trust Deed"
            control={control}
          />
        </Grid>
        <Grid item xs={12}>
          <Trustees>
            <Header>
              <FormLabel className={LabelStyle}>Trustees</FormLabel>
              <Actions>
                <Button
                  startIcon={<MdAdd />}
                  endIcon={<MdPerson/>}
                />
                <Button
                  startIcon={<MdAdd />}
                  endIcon={<BiBuilding/>}
                  onClick={onAddCompany}
                />
              </Actions>
            </Header>
            {
              !trustees.length ?
              <EmptyState>No trustees assigned</EmptyState>
              :
              trustees.map((trustee, index) =>
                isCompanyTrustee(trustee) ? 
                <CompanyTrusteeDetails
                  key={index}
                  index={index}
                  trustee={trustee}
                  onRemove={removeTrustee} />
                :
                <div key={index}>{trustee.familyName}</div>
              )
            }
          </Trustees>
        </Grid>
      </Grid>
      <AddCompanyTrusteeDialog
        isOpen={isCompanyModalOpen}
        onClose={onCompanyModalClose}
        onSubmit={onCompanyModalSubmit}
      />
    </Card>
  )
}

export default TrustDetails;
