import styled from "@emotion/styled";
import { Button, Chip, IconButton, Menu, MenuItem } from "@material-ui/core";
import { css } from "emotion";
import React, { useState } from "react";
import { MdMoreVert } from "react-icons/md";
import { useRecoilState } from "recoil";
import { tradingEntityState } from "state/TradingEntityState";
import { EntityTypeLabel } from "types/EntityType";
import TradingEntity from "types/TradingEntity";
import AddTradingEntityDialog from "./AddTradingEntityDialog";

const EmptyState = styled.div`
  text-align: center;
  padding: 24px;
  background-color: #eee;
`;

const MenuItemStyle = css`
  padding: 10px 16px;
`;

const MainName = styled.div`
  font-size: 14px;
`;

const BusinessType = styled.div`
  position: absolute;
  top: 0;
  left: 50%;
`;

const Abn = styled.div`
  margin-top: 1em;
  color: #777;
`;

const BusinessNames = styled.div`
  margin-top: 1em;
  color: #777;
`;

const BusinessName = styled.div`
  color: #777;
`;

const SelectedTradingEntity = styled.div`
  padding: 24px 16px 16px 16px;
  background-color: #eee;
  position: relative;
`;

const TradingEntityEditClass = css`
  position: absolute;
  right: 2px;
  top: 2px;
`;

const EntityTypeStyle = css`
  margin-left: -50%;
  margin-top: -40%;
`;

function TradingEntityPanel() {
  const [isModalOpen, setIsModalOpen] = useState(false);
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);
  const [tradingEntity, setTradingEntity] = useRecoilState(tradingEntityState);

  function onAssignClick() {
    setIsModalOpen(true);
  }

  const onModalClose = () => {
    setIsModalOpen(false);
  }

  const onModalSubmit = (tradingEntity: TradingEntity) => {
    setIsModalOpen(false);
    setTradingEntity(tradingEntity);
  }

  function onMenuClose() {
    setAnchorEl(null);
  }

  function onMenuRemove() {
    onMenuClose();
    setTradingEntity(undefined);
  }

  function onTradingEntityActionMenuClick(evt: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    setAnchorEl(evt.currentTarget);
  }

  return (
    <div>
      {
        !tradingEntity ?
        <EmptyState>
          {/* <Grid container spacing={2}>
            <Grid item xs={12}>
              No trading entity assigned
            </Grid>
            <Grid item xs={12}> */}
              <Button variant="contained" onClick={onAssignClick}>Assign Trading Entity</Button>
            {/* </Grid>
          </Grid> */}
        </EmptyState> :
        <SelectedTradingEntity>
          <BusinessType>
            <Chip
              className={EntityTypeStyle}
              label={EntityTypeLabel.get(tradingEntity.entityType)}
              variant="filled"
              color="primary"
            />
          </BusinessType>
          <IconButton
            className={TradingEntityEditClass}
            onClick={onTradingEntityActionMenuClick}
          >
            <MdMoreVert />
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            getContentAnchorEl={null}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={onMenuClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
          >
            {/* <MenuItem className={MenuItemStyle} onClick={() => {}}>Run</MenuItem> */}
            <MenuItem className={MenuItemStyle} onClick={onMenuRemove}>Remove</MenuItem>
          </Menu>
          <MainName>{tradingEntity.mainName}</MainName>
          <Abn>{tradingEntity.abn}</Abn>
          <BusinessNames>{tradingEntity.businessNames?.map((businessName, index) => 
            <BusinessName key={index}>{businessName}</BusinessName>
          )}</BusinessNames>
        </SelectedTradingEntity>
      }
      <AddTradingEntityDialog
        isOpen={isModalOpen}
        onClose={onModalClose}
        onSubmit={onModalSubmit}
      />
    </div>
  )
}

export default TradingEntityPanel;
