import { Grid } from "@material-ui/core";
import TextField from "components/form/TextField";
import React from "react";
import { useForm } from "react-hook-form";
import Person from "types/Person";

export interface NewPersonDetailsProps {
  person: Person;
  // onChange: (tradingEntity?: TradingEntity) => void;
}

interface NewPersonDetailsForm {
  FirstGivenName: string;
  OtherGivenName: string;
  FamilyName: string;
  Mobile: string;
  Email: string;
}

function NewPersonDetails({ person: { firstGivenName, otherGivenName, familyName, mobile, email } }: NewPersonDetailsProps) {

  const methods = useForm<NewPersonDetailsForm>({
    defaultValues: {
      FirstGivenName: firstGivenName,
      OtherGivenName: otherGivenName,
      FamilyName: familyName,
      Mobile: mobile,
      Email: email,
    },
    mode: 'onChange',
  });

  const {
    // handleSubmit,
    control,
    // watch,
    // getValues,
  } = methods;

  return (
    <Grid container spacing={2} alignItems="flex-end">
      <Grid item xs={12} md={4}>
        <TextField
          autoFocus
          name="FirstGivenName"
          label="First Given Name"
          control={control}
        />
      </Grid>
      <Grid item xs={12} md={4}>
        <TextField
          name="OtherGivenName"
          label="Other Given Name"
          control={control}
        />
      </Grid>
      <Grid item xs={12} md={4}>
        <TextField
          name="FamilyName"
          label="Family Name"
          control={control}
        />
      </Grid>
    </Grid>
  )
}

export default NewPersonDetails;
