import { isValidABN } from 'abnacn-validator';
import { Autocomplete, createFilterOptions, TextField } from '@material-ui/core';
import styled from '@emotion/styled';
import { css } from 'emotion';
import React from 'react';
import TradingEntity from 'types/TradingEntity';
import EntityType from 'types/EntityType';
import { noSpaces } from 'utils/string';
import { formatAbnAcn } from 'utils/abnAcn';

const Name = styled.span`
  display: block !important;
  font-size: 14px;
`;

const EntityMatch = styled.span`
  display: block;
`;

const Abn = styled.span`
  display: block;
  font-size: 11px;
  color: #666;
  margin-top: 0.5em;
`;

const BusinessName = styled.span`
  display: block;
  font-size: 11px;
  color: #666;
`;

const ListBoxStyles = css`
  .MuiAutocomplete-option {
    padding-top: 8px;
    padding-bottom: 8px;
  }
`;

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

export interface TradingEntityInput extends TradingEntity {
  inputValue?: string;
}

const allTradingEntities: TradingEntityInput[] = [
  {
    abn: '34 826 428 290',
    mainName: 'The Trustee for The A & A White Family Trust',
    entityType: EntityType.Trust,
    entityId: 'e1b05d24-2db8-451b-8812-9b38d2a26f93',
    businessNames: [
      'Advance Human Solutions',
      'Empowering Woman',
    ],
  },
  {
    abn: '63 316 911 021',
    mainName: 'A SMITH & W SMITH & THE TRUSTEE FOR THE SMITH FAMILY TRUST',
    entityType: EntityType.Partnership,
    entityId: '31adab15-ca11-4a81-a619-5ab28811c902',
    businessNames: [
      'CE & H SMITH	'
    ],
  }
];

const filter = createFilterOptions<TradingEntityInput>();

interface TradingEntityPickerProps {
  value: TradingEntityInput | null;
  onChange: (tradingEntity: TradingEntityInput | null) => void;
}

const TradingEntityPicker = ({ value, onChange }: TradingEntityPickerProps) => {
  return (
    <Autocomplete
      value={value}
      autoHighlight
      classes={{
        listbox: ListBoxStyles
      }}
      options={allTradingEntities}
      getOptionSelected={(option: TradingEntityInput, value: TradingEntityInput) => { 
        return value ? option === value : false;
      }}
      renderOption={(props, value, state) => (
        <li {...props}>
          <EntityMatch>
            <Name>{value.mainName}</Name>
            {!!value.abn &&<Abn>{value.abn}</Abn>}
            {!!value.businessNames && <BusinessName>{value.businessNames.join(', ')}</BusinessName>}
          </EntityMatch>
        </li>
      )}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === 'string') {
          return option;
        }
        // Add "xxx" option created dynamically
        if (option.inputValue) {
          return option.inputValue;
        }
        // Regular option
        return `${option.mainName} ${option.abn.replace(/\s/g, '')} ${option.businessNames?.join(' ')}`
      }}
      // getOptionLabel={(option) => `${option.name} ${option.email} ${option.mobile}`}
      // value={value}
      // value={selectedValueWithLabel}
      // onChange={(evt, newValue) => onChange(newValue)}
      onChange={(evt, newValue) => {
        if (typeof newValue === 'string') {
          onChange({
            abn: '',
            entityType: EntityType.None,
            mainName: newValue,
            // email: newValue,
          });
        } else if (newValue) {
          // Create a new value from the user input
          onChange(newValue);
        } else {
          onChange(null);
        }
      }}
      filterOptions={(options, params) => {
        const filtered = filter(options, params);
        const cleanValue = noSpaces(params.inputValue);

        // Suggest the creation of a new value
        if (cleanValue !== '') {
          if (cleanValue.length === 11 && isValidABN(cleanValue)) {
            filtered.push({
              inputValue: cleanValue,
              abn: '',
              entityType: EntityType.None,
              mainName: `Lookup "${formatAbnAcn(cleanValue)}" on ABR Search`
            });
          }
        // } else {
        //   filtered.push({
        //     // inputValue: params.inputValue,
        //     id: 0,
        //     name: '',
        //     email: 'Type email address to Add'
        //   });
        // }
        }

        return filtered;
      }}
      // onBlur={onBlur}
      // freeSolo
      renderInput={({ InputLabelProps, InputProps, ...rest }) => 
        <TextField
          autoFocus={true}
          className={TextFieldStyle}
          // label="Find or create user"
          // error={!!error}
          InputLabelProps={{ shrink: true, ...InputLabelProps }}
          InputProps={{ placeholder: 'Find by ABN or name', ...InputProps }}
          // helperText={ error?.message ?? '' }
          {...rest}
        />
      }
    />
  )
};

export default TradingEntityPicker;
