import { Autocomplete, createFilterOptions, TextField } from '@material-ui/core';
import styled from '@emotion/styled';
import { css } from 'emotion';
import React from 'react';
import Person from 'types/Person';
import { noSpaces } from 'utils/string';

const Name = styled.span`
  display: block !important;
  font-size: 14px;
`;

const EntityMatch = styled.span`
  display: block;
`;

const Email = styled.span`
  margin-top: 0.5em;
  display: block;
  font-size: 11px;
  color: #666;
`;

const Mobile = styled.span`
  display: block;
  font-size: 11px;
  color: #666;
`;

const ListBoxStyles = css`
  .MuiAutocomplete-option {
    padding-top: 8px;
    padding-bottom: 8px;
  }
`;

const TextFieldStyle = css`
  .MuiFormLabel-root {
    font-size: 11px;
    color: #777;
    /* text-transform: uppercase; */
    line-height: 2em;
  }
  .MuiInput-root {
    background-color: #eee;
  }
  .MuiInput-input {
    padding: 6px 5px;
    font-size: 14px;
  }
`;

export interface PersonInput extends Person {
  inputValue?: string;
}

const allPersonEntities: PersonInput[] = [
  {
    firstGivenName: 'Kim',
    familyName: 'Wexler',
    email: 'kim.wexler@wm.com',
    mobile: '+61 444 555 666',
  },
  {
    firstGivenName: 'Saul',
    familyName: 'Goodman',
    email: 'saul.goodman@itsallgoodman.com',
    mobile: '+61 432 109 876',
  },
  {
    firstGivenName: 'Walter',
    familyName: 'White',
    email: 'walter.white@bb.com',
    mobile: '+61 456 789 012'
  }
];

const filter = createFilterOptions<PersonInput>();

interface PersonPickerProps {
  value: PersonInput | null;
  onChange: (tradingEntity: PersonInput | null) => void;
}

const PersonPicker = ({ value, onChange }: PersonPickerProps) => {
  return (
    <Autocomplete
      value={value}
      autoHighlight
      classes={{
        listbox: ListBoxStyles
      }}
      options={allPersonEntities}
      getOptionSelected={(option: PersonInput, value: PersonInput) => { 
        return value ? option === value : false;
      }}
      renderOption={(props, value, state) => {
        return (
          // value.abn === '' && !value.inputValue ? 
          // <span>
          //   <EntityMatch>
          //     <Abn>{formatAbnAcn(value.acn)} {!!value.abn && <> / {formatAbnAcn(value.abn)}</>}</Abn>
          //   </EntityMatch>
          // </span> :
          <li {...props}>
            <EntityMatch>
              <Name>{value.firstGivenName} {value.familyName}</Name>
              {!!value.email && <Email>{value.email}</Email>}
              {!!value.mobile && <Mobile>{value.mobile}</Mobile>}
            </EntityMatch>
          </li>
        )
      }}
      selectOnFocus
      clearOnBlur
      handleHomeEndKeys
      getOptionLabel={(option) => {
        // Value selected with enter, right from the input
        if (typeof option === 'string') {
          return option;
        }
        // Add "xxx" option created dynamically
        if (option.inputValue) {
          return option.inputValue;
        }
        // Regular option
        return `${option.firstGivenName}${!!option.otherGivenName ? ` ${option.otherGivenName}` : ''} ${option.familyName} ${option.mobile} ${option.email}`
      }}
      onChange={(evt, newValue) => {
        if (newValue) {
          if (newValue.inputValue) {
            const nameParts = newValue.inputValue.split(' ');
            onChange({
              firstGivenName: nameParts[0],
              familyName: newValue.inputValue.substring(nameParts[0].length + 1)
            });
          } else {
            onChange(newValue);
          }
        } else {
          onChange(null);
        }
      }}
      filterOptions={(options, params) => {
        const filtered = filter(options, params);
        const cleanValue = noSpaces(params.inputValue);
        // const numericValue = parseInt(cleanValue)
        // const isNumeric = `${numericValue}`.length === cleanValue.length && !isNaN(numericValue);

        // Suggest the creation of a new value
        if (cleanValue !== '') {
          filtered.push({
            inputValue: params.inputValue,
            familyName: '',
            firstGivenName: `Create Person "${params.inputValue}"`
          });
        }

        return filtered;
      }}
      // onBlur={onBlur}
      // freeSolo
      renderInput={({ InputLabelProps, InputProps, ...rest }) => 
        <TextField
          autoFocus={true}
          className={TextFieldStyle}
          // label="Find or create user"
          // error={!!error}
          InputLabelProps={{ shrink: true, ...InputLabelProps }}
          InputProps={{ placeholder: 'Find by Name, Email or Mobile', ...InputProps }}
          // helperText={ error?.message ?? '' }
          {...rest}
        />
      }
    />
  )
};

export default PersonPicker;
