import { Button, Dialog, DialogActions, DialogContent, DialogTitle, Grid, useMediaQuery, useTheme } from '@material-ui/core';
import { css } from 'emotion';
import React, { useEffect } from 'react';
import { useState } from 'react';
import { bp } from 'theme';
import PersonPicker, { PersonInput } from './PersonPicker';
import NewPersonDetails from './NewPersonDetails';

const ContentStyle = css`
  ${bp.xs} {
    width: 100%
  }
  ${bp.sm} {
    width: 580px;
  }
`

// const SelectedEntityChipLabelStyle = css`
//   font-size: 14px;
//   ${bp.xs} {
//     max-width: 340px;
//   }
//   ${bp.sm} {
//     max-width: 500px;
//   }
// `

export interface AddDirectorDialogProps {
  isOpen: boolean;
  // company?: PersonInput;
  onClose: () => void;
  onSubmit: (company: PersonInput) => void;
}

function AddDirectorDialog({ isOpen, onClose, onSubmit }: AddDirectorDialogProps) {
  const [selectedPerson, setSelectedPerson] = useState<PersonInput | null>(null);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xlMob'))

  useEffect(
    () => {
      // Unmount CallBack
      if (!isOpen) {
        reset();
      }
      return () => reset();
    },
    [isOpen]
  )

  function reset() {
    setSelectedPerson(null);
  }

  function onModalClose() {
    onClose();
  }

  function onModalOK() {
    onSubmit(selectedPerson!);
  }

  // function onSelectionRemove() {
  //   setSelectedPerson(null);
  // }

  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        Add Director
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
            {
              (selectedPerson) ?
                <NewPersonDetails
                  person={!!selectedPerson.inputValue ? {
                    firstGivenName: selectedPerson.inputValue,
                    familyName: ''
                  } : selectedPerson}
                />
              :
              <PersonPicker
                value={selectedPerson}
                onChange={setSelectedPerson}
              />
            }
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" disabled={!selectedPerson} onClick={onModalOK}>
          Add
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default AddDirectorDialog;
