import AbrEntityType from './AbrEntityType';

enum TrustType {
  None = 0,
  FixedUnitTrust = 1,
  FixedTrust = 3,
  OtherTrust = 5,
  CorporateUnitTrust = 7,
  DiscretionaryTradingTrust = 9,
  HybridTrust = 11,
  SelfManagedSuperannuationFund = 13,
  DiscretionaryInvestmentTrust = 15,
}

export const TrustTypeFromAbrEntityType = new Map<AbrEntityType, TrustType>([
  [AbrEntityType.None, TrustType.None],
  [AbrEntityType.FixedUnitTrust, TrustType.FixedUnitTrust],
  [AbrEntityType.FixedTrust, TrustType.FixedTrust],
  [AbrEntityType.OtherTrust, TrustType.OtherTrust],
  [AbrEntityType.CorporateUnitTrust, TrustType.CorporateUnitTrust],
  [AbrEntityType.DiscretionaryTradingTrust, TrustType.DiscretionaryTradingTrust],
  [AbrEntityType.DiscretionaryInvestmentTrust, TrustType.DiscretionaryInvestmentTrust],
  [AbrEntityType.HybridTrust, TrustType.HybridTrust],
  [AbrEntityType.SelfManagedSuperannuationFund, TrustType.SelfManagedSuperannuationFund],
]);

export const TrustTypeLabels = new Map<TrustType, string>([
  [TrustType.CorporateUnitTrust, 'Corporate Unit Trust'],
  [TrustType.DiscretionaryInvestmentTrust, 'Discretionary Investment Trust'],
  [TrustType.DiscretionaryTradingTrust, 'Discretionary Trading Trust'],
  [TrustType.FixedTrust, 'Fixed Trust'],
  [TrustType.FixedUnitTrust, 'Fixed Unit Trust'],
  [TrustType.HybridTrust, 'Hybrid Trust'],
  [TrustType.SelfManagedSuperannuationFund, 'Self Managed Super Fund'],
  [TrustType.OtherTrust, 'Other Trust'],
]);

export const isUnitTrustType = (trustType: TrustType): boolean => {
  return trustType === TrustType.FixedUnitTrust ||
    trustType === TrustType.CorporateUnitTrust;
};

export default TrustType;
