import { Button, Chip, Dialog, DialogActions, DialogContent, DialogTitle, Grid, useMediaQuery, useTheme } from '@material-ui/core';
import { css } from 'emotion';
import React, { useEffect } from 'react';
import { useState } from 'react';
import { bp } from 'theme';
import TradingEntityPicker, { TradingEntityInput } from './TradingEntityPicker';

const ContentStyle = css`
  ${bp.xs} {
    width: 100%
  }
  ${bp.sm} {
    width: 580px;
  }
`

const SelectedEntityChipLabelStyle = css`
  font-size: 14px;
  ${bp.xs} {
    max-width: 340px;
  }
  ${bp.sm} {
    max-width: 500px;
  }
`

export interface AddTradingEntityDialogProps {
  isOpen: boolean;
  // tradingEntity?: TradingEntityInput;
  onClose: () => void;
  onSubmit: (tradingEntity: TradingEntityInput) => void;
}

function AddTradingEntityDialog({ isOpen, onClose, onSubmit }: AddTradingEntityDialogProps) {
  const [selectedTradingEntity, setSelectedTradingEntity] = useState<TradingEntityInput | null>(null);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('xlMob'))

  useEffect(
    () => {
      // Unmount CallBack
      if (!isOpen) {
        reset();
      }
      return () => reset();
    },
    [isOpen]
  )

  function reset() {
    setSelectedTradingEntity(null);
  }

  function onModalClose() {
    onClose();
  }

  function onModalOK() {
    onSubmit(selectedTradingEntity!);
  }

  function onSelectionRemove() {
    setSelectedTradingEntity(null);
  }
  

  return (
    <Dialog
      fullScreen={fullScreen}
      open={isOpen}
      onClose={onModalClose}
      aria-labelledby="alert-dialog-title"
      aria-describedby="alert-dialog-description"
    >
      <DialogTitle id="alert-dialog-title">
        Find Trading Entity
      </DialogTitle>
      <DialogContent className={ContentStyle}>
        <Grid container spacing={2}>
          <Grid item xs={12}>
          {
              (selectedTradingEntity) ?
              <Chip
                classes={{ label: SelectedEntityChipLabelStyle }}
                label={selectedTradingEntity.inputValue ? selectedTradingEntity.inputValue : `${selectedTradingEntity.mainName} (${selectedTradingEntity.abn})`}
                onDelete={onSelectionRemove}
              />
              :
              <TradingEntityPicker
                value={selectedTradingEntity}
                onChange={setSelectedTradingEntity}
              />
            }
            {/* <TextField
              name="Abn"
              control={control}
              // label="Name"
              placeholder="Search by ABN"
              // rules={ { required: 'Required' } }
            /> */}
          </Grid>
        </Grid>
      </DialogContent>
      <DialogActions>
        <Button onClick={onModalClose}>
          Cancel
        </Button>
        <Button variant="contained" disabled={!selectedTradingEntity} onClick={onModalOK}>
          Select
        </Button>
      </DialogActions>
    </Dialog>
  )
}

export default AddTradingEntityDialog;
