const nonDigits = /[^\d]/g;

export const sanitizeAbnAcn = (value: string | null | undefined): string =>
  value ? value.replace(nonDigits, '') : '';

export const formatAbnAcn = (value: string | null | undefined): string => {
  if (!value) {
    return '';
  }
  const sanitizedValue = sanitizeAbnAcn(value);

  if (sanitizedValue.length === 11) {
    return formatAbn(sanitizedValue);
  }

  if (sanitizedValue.length === 9) {
    return formatAcn(sanitizedValue);
  }

  return value;
};

export const formatAbn = (value: string): string => {
  return `${value.slice(0, 2)} ${value.slice(2, 5)} ${value.slice(5, 8)} ${value.slice(8, 11)}`;
};

export const formatAcn = (value: string): string => {
  return `${value.slice(0, 3)} ${value.slice(3, 6)} ${value.slice(6, 9)}`;
};