import Company from "./Company";
import Person from "./Person";
import TrustType from "./TrustType";

export interface UnitTrust {
  unitHolders?: Person[];
  foundingUnitHolders?: Person[];
}

export interface PersonTrustee extends Person {

}

export interface CompanyTrustee extends Company {

}

export interface CompanySettlor extends Company {

}

export interface PersonSettlor extends Company {

}

export type Trustee = PersonTrustee | CompanyTrustee;
export type Settlor = PersonSettlor | CompanySettlor;

export const isCompanyTrustee = (trustee: Trustee): trustee is CompanyTrustee => {
  const isCompany = (trustee as CompanyTrustee).acn !== undefined;
  return isCompany;
}

export const isPersonTrustee = (trustee: Trustee): trustee is PersonTrustee => {
  return (trustee as PersonTrustee).firstGivenName !== undefined
}

export interface Trust {
  legalName: string;
  abn?: string;
  trustees?: Trustee[];
  unitTrust?: UnitTrust;
  settlor?: Settlor;
  deedDate?: string;
  trustType?: TrustType;
}
