import styled from "@emotion/styled";
import React from "react";
import { CompanyTrustee } from "types/Trust";
import { BiBuilding } from "react-icons/bi";
import { MdMoreVert } from "react-icons/md";
import { Grid, IconButton, Menu, MenuItem } from "@material-ui/core";
import { css } from "emotion";
import { formatAbnAcn } from "utils/abnAcn";

const TrusteeRoot = styled.div`
  background-color: #eee;
  margin-bottom: 2px;
`;

const Head = styled.div`
  display: flex;
`;

const Title = styled.div`
  flex-grow: 1;
  padding: 16px;
  display: flex;
`;

const CoreDetails = styled.div`
  flex-grow: 1;
`;

const Name = styled.span`
  display: inline-block;
  font-size: 14px;
`;

const Acn = styled.span`
  display: inline-block;
  color: #777;
`;

const Icon = styled.span`
  display: inline-block;
  font-size: 16px;
  width: 30px;
  vertical-align: middle;
`;

const Actions = styled.div`
  margin-right: 2px;
  margin-top: 2px;
`;

const MenuItemStyle = css`
  padding: 10px 16px;
`;

export interface CompanyTrusteeDetailsProps {
  trustee: CompanyTrustee;
  onRemove: (index: number) => void;
  index: number;
}

function CompanyTrusteeDetails({ trustee, onRemove, index}: CompanyTrusteeDetailsProps) {
  const [anchorEl, setAnchorEl] = React.useState<HTMLButtonElement | null>(null);

  function onMenuClose() {
    setAnchorEl(null);
  }

  function onMenuRemove() {
    onMenuClose();
    onRemove(index);
  }

  function onMenuRunCreditCheck() {
    onMenuClose();
    // onRemove(index);
  }

  function onMenuClick(evt: React.MouseEvent<HTMLButtonElement, MouseEvent>) {
    setAnchorEl(evt.currentTarget);
  }

  return (
    <TrusteeRoot>
      <Head>
        <Title>
          <Icon>
            <BiBuilding/>
          </Icon>
          <CoreDetails>
            <Grid container spacing={2}>
              <Grid item xs={12}>
                <Name>{trustee.name}</Name>
              </Grid>
              <Grid item xs={12}>
                <Acn>
                  <span>{formatAbnAcn(trustee.acn)}</span>
                  { !!trustee.abn && <> / <span>{formatAbnAcn(trustee.abn)}</span></>}
                </Acn>
              </Grid>
            </Grid>
          </CoreDetails>
        </Title>
        <Actions>
          <IconButton
            // className={TradingEntityEditClass}
            onClick={onMenuClick}
          >
            <MdMoreVert />
          </IconButton>
          <Menu
            anchorEl={anchorEl}
            getContentAnchorEl={null}
            keepMounted
            open={Boolean(anchorEl)}
            onClose={onMenuClose}
            anchorOrigin={{
              vertical: 'bottom',
              horizontal: 'right',
            }}
            transformOrigin={{
              vertical: 'top',
              horizontal: 'right',
            }}
          >
            {/* <MenuItem className={MenuItemStyle} onClick={() => {}}>Run</MenuItem> */}
            <MenuItem className={MenuItemStyle} onClick={onMenuRunCreditCheck}>Run Credit Check</MenuItem>
            <MenuItem className={MenuItemStyle} onClick={onMenuRemove}>Remove</MenuItem>
          </Menu>
        </Actions>
      </Head>
    </TrusteeRoot>
  )
}

export default CompanyTrusteeDetails;
